// Add your settings here
$container-gap: 48px;
$container-fluid-gap: 48px;
$container-gap-mobile: 25px;
$container-fluid-gap-mobile: 25px;
$conteiner-fluid-width: 1616px;

* {
	box-sizing: border-box;
	margin: 0;
	padding: 0; }

html, body {
	position: relative;
	height: 100%; }

.wrapper {
	min-height: 100vh;
	position: relative;
	z-index: 1;
	display: flex;
	flex-direction: column;
	align-items: stretch;
	justify-content: flex-start;
	overflow: hidden; }

article {
	overflow: hidden; }

footer {
	margin: auto 0 0; }

// need to add "html {overflow: hidden}",  when disable scroll -> for ios

.body-overflow {
	overflow: hidden;
	overscroll-behavior-y: none;
	-webkit-overflow-scrolling: auto; }

.container {
	max-width: 1236px;
	width: 100%;
	position: relative;
	margin: 0 auto;
	padding: 0 $container-gap;
	@include to(374) {
		padding: 0 $container-gap-mobile; }
	&_fluid {
		max-width: $conteiner-fluid-width;
		padding: 0 $container-fluid-gap;
		@include to(374) {
			padding: 0 $container-fluid-gap-mobile; } } }


.sr-only,
select { // content made available only to screen readers
	position: absolute;
	clip: rect(1px, 1px, 1px, 1px);
	clip-path: inset(50%);
	width: 1px;
	height: 1px;
	overflow: hidden;
	padding: 0;
	border: 0;
	white-space: nowrap; }

.contain-picture {
	object-fit: contain!important; }

a {
	text-decoration: none;
	color: inherit;
	background-color: transparent;
	-webkit-text-decoration-skip: objects; }

