$header-top-gap: 82px;
$header-logo-height: 51px;
$header-navigation-size: 25px;
$header-desktop-navigation-width: calc(260px + (100vw - #{$conteiner-fluid-width}) / 2);
$header-desktop-navigation-offside-transform: calc((100vw - #{$conteiner-fluid-width}) / 2 + 22px * 1.5);
$header-desktop-navigation-width-mobile: 260px;
$header-navigation-list-gap: $header-navigation-size + 20px;
$header-navigation-container-gap: 55px;

.header {
  position: fixed;
  z-index: 10;
  pointer-events: none;
  top: 0;
  left: 0;
  width: 100%;
  padding-top: $header-top-gap;
  padding-bottom: 20px;
  @include r(374) {
    padding-top: 50px; }
  &__container {}
  &__content {
    display: flex;
    align-items: stretch;
    justify-content: space-between; }
  &__logo {
    width: 62px;
    height: $header-logo-height;
    color: $white;
    svg {
      width: 100%;
      height: 100%; } }
  &__navigation {
    position: relative;
    display: flex;
    align-items: center;
    width: $header-navigation-size; }
  &__burger {
    display: none;
    position: relative;
    z-index: 2;
    width: $header-navigation-size;
    height: $header-navigation-size;
    border: 0;
    cursor: pointer;
    background-color: transparent;
    @include r(1599) {
      display: block; }
    &.is-active {
      @include r(1599) {
        .header__burger-line {
          background-color: $dark-purple; } } } }
  &__burger-line {
    position: absolute;
    display: block;
    width: 100%;
    height: 2px;
    background-color: $white;
    left: 0;
    transition: background-color $transition-time;
    &:nth-child(1) {
      top: 0; }
    &:nth-child(2) {
      top: 50%;
      transform: translateY(-50%); }
    &:nth-child(3) {
      bottom: 0; } }

  &__navigation-container {
    flex-shrink: 0;
    position: absolute;
    top: 0;
    right: 0;
    height: 100vh;
    width: $header-desktop-navigation-width;
    transform: translate($header-desktop-navigation-offside-transform, -$header-top-gap);
    display: flex;
    align-items: flex-start;
    justify-content: center;
    flex-direction: column;
    padding: $header-navigation-container-gap 0 30px;
    transition: transform $transition-time;
    @include r(1639) {
      right: 0;
      width: $header-desktop-navigation-width-mobile;
      transform: translate(calc(100% + #{$container-fluid-gap}), -$header-top-gap); }
    &.is-active {
      @include r(1599) {
        transform: translate($container-fluid-gap, -$header-top-gap); } } }

  &__navigation-list {
    list-style: none;
    counter-reset: menu;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    justify-content: center;
    padding: $header-navigation-list-gap 6.5vw $header-navigation-list-gap 0;
    @include r(1919) {
      padding: $header-navigation-list-gap 100px $header-navigation-list-gap 0; }
    @include r(1639) {
      background-color: $white;
      align-items: flex-start;
      padding: $header-navigation-list-gap 30px; } }
  &__navigation-list-item {
    margin-bottom: 47px;
    counter-increment: menu;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    color: $white;
    @include pr;
    font-size: 16px;
    pointer-events: auto;
    @include r(1599) {
      color: $dark-purple; }
    &::before {
      content: counter(menu);
      color: inherit;
      font-family: inherit;
      font-size: inherit;
      font-weight: inherit;
      margin-right: 18px; } }
  &__navigation-list-item-line {
    width: 24px;
    height: 1px;
    background-color: $white;
    margin-right: 10px;
    @include r(1599) {
      background-color: $dark-purple; } }
  &__navigation-list-item-link {
    color: inherit;
    font-family: inherit;
    font-size: inherit;
    font-weight: inherit; } }
