.about {
  padding: 200px 0 140px;
  background-color: $white;
  @include r(1023) {
    padding: 150px 0 60px; }
  &__section-title {
    margin-bottom: 70px; }
  &__title {
    max-width: 900px;
    margin-bottom: 60px;
    color: $yellow;
    @include r(1023) {
      margin-bottom: 30px; } }
  &__invest-in {
    margin-bottom: 40px;
    @include r(1023) {
      margin-bottom: 20px; } }
  &__tile {
    display: grid;
    gap: 20px;
    grid-template-columns: repeat(4, 1fr);
    @include r(1023) {
      grid-template-columns: 1fr; } }
  &__tile-item {
    max-width: 230px;
    &:first-child {
      .about__tile-line {
        display: block; } } }
  &__tile-line {
    width: 68px;
    height: 1px;
    display: block;
    background-color: $yellow;
    &:first-of-type {
      @include r(1023) {
        display: none; } }
    &:last-child {
      margin-bottom: 0; } }
  &__tile-row {
    margin-bottom: 20px; } }
