.section-title {
  display: inline-flex;
  justify-content: flex-start;
  align-items: center;
  @include pl;
  color: $yellow;
  font-weight: 600;
  &__number,
  &__name {
    font-size: inherit;
    font-weight: inherit;
    color: inherit;
    font-family: inherit; }
  &__number,
  &__line {
    margin-right: 10px; }
  &__line {
    width: 24px;
    height: 1px;
    background-color: $yellow; } }
