.wwd {
  padding: 105px 0 150px;
  background-color: $dark-purple;
  @include r(1023) {
    padding: 105px 0 75px; }
  &__section-title {
    margin-bottom: 60px;
    @include r(1023) {
      margin-bottom: 40px; } }
  &__tile {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-start; }
  &__tile-item {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    margin-bottom: 130px;
    @include r(1365) {
      margin-bottom: 80px; }
    @include r(767) {
      margin-bottom: 45px;
      flex-wrap: wrap; }
    &:last-child {
      margin-bottom: 0; } }

  &__tile-item-name,
  &__tile-item-title,
  &__tile-item-line {
    flex-shrink: 0; }
  &__tile-item-name {
    position: relative;
    margin-right: 70px;
    @include r(1365) {
      margin-right: 50px; }
    @include r(1023) {
      margin-right: 35px; }
    @include r(767) {
      margin-right: 20px;
      margin-bottom: 20px;
      align-self: stretch;
      display: flex;
      align-items: center; } }
  &__tile-item-name-image {
    max-width: 110px;
    height: 110px;
    object-fit: cover;
    position: absolute;
    z-index: 0;
    right: 0;
    top: 50%;
    transform: translateY(-50%);
    width: auto;
    height: auto;
    @include r(1365) {
      display: none; } }
  &__tile-item-name-text {
    position: relative;
    color: $yellow; }
  &__tile-item-title {
    color: $yellow;
    margin-right: 40px;
    max-width: 300px;
    width: 100%;
    @include r(1023) {
      max-width: 220px;
      margin-right: 20px; }
    @include r(767) {
      max-width: 300px;
      width: auto;
      margin-right: 0;
      margin-bottom: 20px; }
    @include r(479) {
      max-width: 220px; } }
  &__tile-item-line {
    width: 40px;
    height: 1px;
    background-color: $yellow;
    margin-right: 20px;
    @include r(767) {
      display: none; } }
  &__tile-item-text {
    color: $white;
    @include pl;
    font-weight: 600;
    p {
      color: inherit;
      font-weight: inherit;
      font-size: inherit;
      font-family: inherit;
      line-height: inherit;
      @include r(767) {
        margin-bottom: 25px;
        &:last-child {
          margin-bottom: 0; } } } } }
