.hero {
  min-height: 100vh;
  height: 100vh;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  padding: 200px 0 110px;
  @include r(1023) {
    padding: 230px 0 100px;
    height: auto;
    justify-content: space-between; }
  @include r(374) {
    padding: 150px 0 50px; }
  &__bg {
    position: absolute;
    z-index: -1;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    &::after {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: rgba($dark-purple, 0.35); }
    img {
      width: 100%;
      height: 100%;
      object-fit: cover; } }
  // &__footer
  //   +r(1023)
  //     margin-top: auto
  &__content {
    max-width: 1150px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    margin-bottom: 90px;
    @include r(1919) {
      margin-bottom: 50px; }
    @include r(374) {
      margin-bottom: 35px; } }
  &__title,
  &__subtitle {
    color: $white;
    font-weight: 600; }
  &__title {
    margin-bottom: 100px;
    @include r(1919) {
      margin-bottom: 100px; }
    @include r(1023) {
      margin-bottom: 55px; }
    @include r(374) {
      margin-bottom: 25px; } }
  &__scroll-button {
    border: 0;
    background-color: transparent;
    cursor: pointer;
    display: flex;
    align-items: center; }
  &__scroll-button-text {
    color: $yellow;
    margin-right: 20px; }
  &__scroll-button-icon {
    position: relative;
    height: 60px;
    width: 10px;
    &::before {
      content: "";
      position: absolute;
      top: 0;
      left: 50%;
      transform: translateX(-50%);
      width: 1px;
      height: 100%;
      background-color: $yellow; }
    &::after {
      content: "";
      width: 12px;
      height: 12px;
      display: block;
      border: 1px solid $yellow;
      border-radius: 50%;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      animation: circleRun 3s infinite; } } }

@keyframes circleRun {
  from, to {
    transform: translate(-50%, -25px); }
  50% {
    transform: translate(-50%, 10px); } }
