@mixin r($screen) {
	@media all and (max-width: $screen+'px') {
		@content;
	}
}

@mixin from($screen) {
	@media all and (min-width: $screen+'px') {
		@content;
	}
}

@mixin fromTo($screen1, $screen2) {
	@media all and (max-width: $screen1+'px') and (min-width: $screen2+'px') {
		@content;
	}
}

@mixin to($screen) {
	@media all and (max-width: $screen+'px') {
		@content;
	}
}

@mixin b($screen) {
	@media all and (max-height: $screen+'px') {
		@content;
	}
}

// px to rem
@function rem($pixels, $context: $mainFontSize) {
  @if (unitless($pixels)) {
    $pixels: $pixels * 1px;
  }
  @if (unitless($context)) {
    $context: $context * 1px;
  }
  @return $pixels / $context * 1rem;
}


@mixin font-face($font-family, $file-path, $weight: normal, $style: normal, $asset-pipeline: false) {
	@font-face {
		font-family: $font-family;
		font-weight: $weight;
		font-style: $style;
		font-display: swap;
		src: url('#{$file-path}#{$font-family}.eot');
		src: url('#{$file-path}#{$font-family}.woff2') format('woff2');
	}
}